import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input } from 'antd';
import { getGroupsById, createGroups, updateGroups } from 'services/groupManagement';

@Form.create({ name: 'formGroup' })
class FormGradedExercises extends Component {
  constructor(props) {
    super(props);
    this.state = {
      groupId: null,
      initialValue: {},
    };
  }

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ groupId: match.params.id });
      this.getGroupById(match.params.id);
    }
  };

  getGroupById = async id => {
    const res = await getGroupsById(id);
    const initialValue = this.handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleInitialValue = initialValue => {
    const initialValueDefault = {
      label: '',
    };
    return { ...initialValueDefault, ...initialValue };
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { groupId } = this.state;
    try {
      if (!groupId) {
        await createGroups(values);
      } else {
        await updateGroups({ id: groupId, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { groupId } = this.state;
    return (
      <Card title={groupId ? 'Cập nhật hội' : 'Thêm hội mới'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item {...formItemLayout} label="Tên hội" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormGradedExercises {...props} />
  </Page>
));



