import { request } from 'utils';

const prefix = '/api/students';

export const getGroups = ({ page, pageSize = 10, orderBy, isPagingEnabled = true }) =>
  request({
    prefix,
    url: '/Group',
    method: 'GET',
    params: { page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getGroupsById = id =>
  request({
    prefix,
    url: `/Group/${id}`,
    method: 'GET',
  });

export const createGroups = ({ label }) =>
  request({
    prefix,
    url: '/Group',
    data: { label },
    method: 'POST',
  });

export const deleteGroups = id =>
  request({
    prefix,
    url: `/Group/${id}`,
    method: 'DELETE',
  });

export const updateGroups = ({ id, label }) =>
  request({
    prefix,
    url: `/Group/${id}`,
    data: { id, label },
    method: 'PUT',
  });



